import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Subscription } from 'rxjs';
import { ITask } from '~/app/models/ITask.interface';
import { IUser } from '~/app/models/IUser.interface';
import { IUserInfo } from '~/app/models/IUserInfo.inteface';
import { UserService } from '~/app/services/user.service';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss']
})
export class UserComponent implements OnInit, OnDestroy {
  public isLoading: boolean = true;
  public user: IUser | null = null;
  private _userId: string = "";
  public tasks: ITask[] = [];


  private _userSubcription: Subscription | null = null;

  constructor( 
    private _route: ActivatedRoute,
    private _userService: UserService
  ) { }
  
  disableChanges(e: Event):void {
    e.preventDefault();
  }

  sortTasksByStatus(tasks: ITask[]) {
    return tasks.slice().sort((firstTask, secondTask )=> {
      if(firstTask.completed && secondTask.completed) {
        return firstTask.id - secondTask.id;
      }

      if(!firstTask.completed && !secondTask.completed) {
        return firstTask.id - secondTask.id;
      }
      
      return firstTask.completed ? 1: -1
    });
  }

  ngOnInit() {
    this._route.params.subscribe((params: Params) => {
      this._userId = params.id;
    })
    this._userSubcription = this._userService.getUserInfo(this._userId).subscribe(( userInfo: IUserInfo | null ) => {
      this.isLoading = false;
      if(userInfo) {
        this.user = userInfo.user;
        this.tasks = this.sortTasksByStatus(userInfo.tasks);
      } else {
        this.user = null;
        this.tasks = [];
      }
    })
  }
  
  ngOnDestroy() {
    this._userSubcription?.unsubscribe();
  }
}
